<template>
    <div>
        <transition-group name="list" tag="ul">
            <li v-for="(item, index) in items" :key="item.killPerMin" class="shadow">
                <span style="font-size: 0.6rem; font-weight: bold; text-align: center;">
                    {{"소요 시간 : " + item.lapsed + " / 분당 킬 수 : " + item.killPerMin}}
                </span>
                <span class="removeBtn" @click="removeCount({item, index})" style="cursor: pointer">
                    <i class="fas fa-trash alt"></i>
                </span>
            </li>
        </transition-group>
    </div>
</template>

<script>
import {mapGetters, mapMutations} from 'vuex';

export default {
    methods: {
        ...mapMutations([
            'removeCount',
        ]),
    },
    computed: {
        ...mapGetters([
            'items',
        ]),
    },
}
</script>

<style scoped>
ul{
    list-style-type: none;
    padding-left: 0px;
    margin-top: 5.5rem;
    text-align: left;    
}
li{
    display: flex;
    min-height: 50px;
    height: 50px;
    line-height: 50px;
    margin: 0.5rem 0;
    padding: 0 0.9rem;
    background: white;
    border-radius: 5px;
}
.removeBtn{
    margin-left: auto;
    color: #DE4343;
}
.list-enter-active, .list-leave-active {
  transition: all 1s;
}
.list-enter, .list-leave-to /* .list-leave-active below version 2.1.8 */ {
  opacity: 0;
  transform: translateY(30px);
}
</style>



