<template>
    <div>
            <h2 id="time" v-html="time"></h2>
        <div class="inputBox shadow">   
            <input type="text" style="font-weight: bold;" v-model="startCount" @keyup.enter="startBtn" autofocus placeholder="start kill count">
            <span class="addContainer addBtn" @click="startBtn" style="cursor: pointer;">
                start
            </span>
        <div class="inputBox shadow">
            <input type="text" style="font-weight: bold;" v-model="endCount" @keyup.enter="endBtn" placeholder="end kill count" readonly>
            <span class="addContainer addBtn" @click="endBtn" style="cursor: pointer; background: gray;">
                result
            </span>
        </div>
        <modal v-if="showModal" @close="showModal = false">
            <h3 slot="header">close 
                <i class="fas fa-times closeModalBtn" style="cursor: pointer" @click="showModal = false"></i>
            </h3>
            <h5 slot="body">
                입력 란이 비어있습니다.
            </h5>
        </modal>
        </div>
    </div>
</template>

<script>
import Modal from './common/Modal.vue';

export default {
    data() {
        return {
            startCount: "",
            endCount: "",
            showModal: false,
            state: "started",
            startTime: "",
            currentTime: "",
            interval: null,
            startBtnFlag: false,
            endBtnFlag: true,
        }
    },
    destroyed() {
        clearInterval(this.interval);
    },
    computed: {
        time() {
            return this.hours + ':' + this.minutes + ':' + this.seconds;
        },
        milliseconds() {
            return this.$data.currentTime - this.$data.startTime;
        },
        hours() {
            const lapsed = this.milliseconds;
            const hrs = Math.floor((lapsed / 1000 /60 / 60));
            return hrs >= 10 ? hrs: '0' + hrs;
        },
        minutes() {
            const lapsed = this.milliseconds;
            const min = Math.floor((lapsed / 1000 / 60) % 60);
            return min >= 10 ? min : '0' + min;
        },
        seconds() {
            const lapsed = this.milliseconds;
            const sec = Math.ceil((lapsed / 1000) % 60);
            return sec >= 10 ? sec : '0' + sec;
        },
    },
    methods: {
        endBtn() {
            if(!this.endBtnFlag) {
                if(this.endCount !== "") {
                    const killCount = this.endCount - this.startCount;
                    const lapsed = this.hours + ':' + this.minutes + ':' + this.seconds;
                    const killPerMin = 
                        (killCount / (this.milliseconds / 1000 / 60)).toFixed(2);
                    this.$store.commit('killCounter', {lapsed, killPerMin});
                    this.endCount = "";
                }else {
                    this.showModal = !this.showModal;
                }
            }
        },
        startBtn() {
            if(!this.startBtnFlag) {
                if(this.startCount !== "") {
                    this.startBtnFlag = !this.startBtnFlag;
                    this.endBtnFlag = !this.endBtnFlag;
                    $('span').eq(0).css("background", "gray");
                    $('div').find('input').eq(0).attr('readonly', true);
                    $('span').eq(1).css("background", "red");
                    $('div').find('input').eq(1).attr('readonly', false).focus();
                    this.$data.startTime = Date.now();
                    this.interval = setInterval(this.updateCurrentTime, 100);
                }else {
                    this.showModal = !this.showModal;
                }
            }
        },
        reset() {
            this.$data.state = "pauesed";
            this.$data.startTime = Date.now();
            this.$data.currentTime = Date.now();
        },
        pause() {
            this.$data.state = "paused";
        },
        resume() {
            this.$data.state = "started";
        },
        updateCurrentTime() {
            if(this.$data.state === "started") {
                this.currentTime = Date.now();
            }
        }
    },
    components: {
        Modal,
    }
}
</script>

<style scoped>
input:focus {
    outline: none;
}
.inputBox{
    background: white;
    height: 50px;
    line-height: 50px;
    border-radius: 5px;
}
.inputBox input{
    border-style: none;
    font-size: 0.9rem;
    margin-bottom: 2rem;
}
.addContainer{
    float: right;
    background: blue;
    display: block;
    width: 3rem;
    border-radius: 0 5px 5px 0;
}
.addBtn{
    color: white;
    vertical-align: middle;
    font-size: 0.8rem;
}
.closeModalBtn{
    color: red;
}
</style>



