<template>
    <div>
        <div class="clearAllContainer">
            <span class="clearAllBtn" @click="clearCounts" style="cursor: pointer">
                Clear
            </span>
        </div>
        <div>
            <p><br/>
                <br/>start kill count 입력란에 시작 마릿수 입력<br/>
                         end kill count 입력란에 최종 마릿수 입력<br/>
                         소요시간과 분당 킬 수를 알려드립니다.<br/><br/>
                         by 브레디서버 섬유
                         
            </p>

        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex';

export default {
    methods: {
        ...mapMutations([
            'clearCounts',
        ]),
    },
}
</script>

<style scoped>
p {
    font-size: 0.75rem;
    text-align: center;
}
.clearAllContainer{
    width: 8.5rem;
    height: 50px;
    line-height: 50px;
    background-color: white;
    border-radius: 5px;
    margin: 0 auto;
}
.clearAllBtn{
    color: #E20303;
    display: block;
}
</style>



