const state = {
    items: [],
}

const getters = {
    items(state) {
        return state.items;
    }
}

const mutations = {
    killCounter(state, payload) {
        const obj = {lapsed: payload.lapsed, killPerMin: payload.killPerMin};
        state.items.push(obj);
    },
    removeCount(state, payload) {
        state.items.splice(payload.index, 1);
    },
    clearCounts(state) {
        state.items = [];
    }
}

export default {
    state,
    getters,
    mutations,
}


